			</van-popup>
			<!--出入类型-->
			<van-field v-model="typevalue" center input-align="right" left-icon="shop-o" is-link name="出入类型" label="出入类型" readonly placeholder="请选择出入类型" @click="typePicker = true"></van-field>
			<van-popup v-model="typePicker" round position="bottom">
