			</van-popup>
			
			<!--入校日期-->
			<van-field v-model="intimevalue" center input-align="right" left-icon="clock-o" is-link name="入校日期" label="入校日期" readonly placeholder="请选择入校日期" @click="intimePicker = true"></van-field>
