<script>
	export default{
		data(){
			return{
				schvalue:'',
				typevalue:'',
				intimevalue:'',
				costvalue:'',
				costwayvalue:'',
				schPicker: false,
				typePicker: false,
				intimePicker:false,
				costPicker:false,
				costwayPicker:false,
				minDate: new Date(2020, 0, 1),
				maxDate: new Date(2025, 10, 1),
				currentDate:new Date(),
				schList: ['中财','北航','北邮','北师大'],
				typeList: ['园区','图书馆','场馆','餐厅'],
				costList: ['5元','10元','15元'],
				costwayList: ['在线支付','线下支付'],
				 username: '',
				 password: '',
			}
		},
		methods:{
			//出入学校
			schSelect(schvalue) {
			      this.schvalue = schvalue;
			      this.schPicker = false;
			},
			//出入类型
			typeSelect(typevalue) {
			      this.typevalue = typevalue;
			      this.typePicker = false;
			},
			//入校日期
			intimeSelect(value) {
				  let date = new Date(value)
				  let year = date.getFullYear() + '-'
				  let month = date.getMonth()+1 + '-'
				  let day = date.getDate()
				  let newdate = year+month+day
				  this.intimevalue = newdate
			      this.intimePicker = false;
			},
			//收费金额
			costSelect(costvalue) {
			      this.costvalue = costvalue;
			      this.costPicker = false;
			},
			//收费方式
			costwaySelect(costwayvalue) {
			      this.costwayvalue = costwayvalue;
			      this.costwayPicker = false;
			},
			//提交
			onSubmit(values) {
			   console.log(typeof(values));
			   if(this.schvalue == '' || this.typevalue == '' || this.intimevalue == '' || this.costvalue == '' || this.costwayvalue == ''){
				   this.$toast.fail({
					   message: '信息录入不全！',
					   icon: 'clear',
				   })
			   }else{
				   // 成功提交
				   this.$notify({ type: 'success', message: '预约提交成功' });
			   }
			},
		}
	}
</script>

